<template>
  <NavMenu />
  <Border>
    <div id="main-grid">
      <header id="site-header">
        <h2 id="page-title">{{ checkTitle() }}</h2>
      </header>
      <router-view id="main-content" />
      <SiteFooter id="site-footer" />
    </div>
  </Border>
</template>

<script>
import Border from "@/components/site/border.vue";
import NavMenu from "@/components/site/nav-menu.vue";
import SiteFooter from "@/components/site/site-footer";
import { pageNames } from "@/router/index.ts";

export default {
  name: "App",
  components: {
    Border,
    SiteFooter,
    NavMenu,
  },

  methods: {
    getProps() {
      return self.props;
    },
    checkTitle() {
      if (pageNames.includes(this.$route.name)) {
        if (this.$route.name == "home") return "/";
        return this.$route.path;
      } else return "";
    },
  },
};
</script>

