
import { defineComponent } from "vue";
import wavesurf from "@/components/audio/wavesurf.vue";
import JkButton from "../site/JkButton.vue";

interface Sound {
  name: string;
  extension: string | null;
  url: string;
}

export default defineComponent({
  components: {
    wavesurf,
    JkButton,
  },
  data: () => ({
    activeSound: "" as string,
    sounds: new Map() as Map<string, Sound>,
    soundsIterator: null as any,
    soundListElements: {} as { [key: string]: HTMLLIElement },
    initialized: false as Boolean,
    autoplay: true as Boolean,
  }),
  emits: ["playerReady"],
  async mounted() {
    this.getNewSoundList();
  },
  methods: {
    async getNewSoundList() {
      const server = "https://jkcraft.duckdns.org:14500/";

      let r = await fetch(server + "indices.json");
      let indices: any = await r.json();

      for (const sound of indices["audio"]) {
        let temp = sound.split("/").pop().split(".");
        const ext = temp.pop();
        let s = temp.join(".");
        const thisSound: Sound = {
          name: s,
          extension: ext,
          url: server + sound,
        };
        this.sounds.set(s, thisSound);
      }
      this.soundsIterator = this.sounds.keys();
      this.activeSound = this.soundsIterator.next().value;
      this.$emit("playerReady");
      this.initialized = true;
    },
    playpause() {
      (this.$refs.wave as any).togglePlay();
    },
    updateSounds(url: any) {
      return url;
    },
    selectSound(soundpath: string) {
      //ensure there's no cross-play fuckery
      this.activeSound = soundpath;
    },
    selectNextSound() {
      let next = this.soundsIterator.next();
      if (next.done) {
        this.soundsIterator = this.sounds.keys();
      }
      this.selectSound(next.value);
    },
    createSoundlistRefs(el: HTMLLIElement) {
      if (!el) {
        //needed to prevent error on page unmount. figure this out later.
        return;
      }
      this.soundListElements[el.innerText] = el;
    },
    isSelectedSound(soundpath: string) {
      return soundpath == this.activeSound;
    },
  },
});
