<template>
  <div class="wavesurf" :class="{ loaded: loaded }">
    <!-- {{ loaded ? "" : "LOADING" }} -->
  </div>
</template>

<script>
import WaveSurfer from "wavesurfer.js";

export default {
  props: { file: null },
  emits: ["finished"],
  data: () => ({
    wavesurfer: null,
    loaded: false,
    wasPlaying: false,
    options: {
      container: ".wavesurf",
      barWidth: 2,
      audioRate: 1,
      backgroundColor: "white",
      progressColor: "black",
      waveColor: "#dadada",
      height: 130,
      responsive: true,
    },
  }),
  mounted() {
    if (!this.wavesurfer) this.createWaveSurfer();
    this.loadNewWave(this.$props.file);
  },
  beforeUnmount() {
    this.wavesurfer.destroy();
  },
  methods: {
    createWaveSurfer() {
      this.wavesurfer = WaveSurfer.create(this.options);
      this.wavesurfer.on("loading", () => {
        this.loaded = false;
      });
      this.wavesurfer.on("ready", () => {
        this.loaded = true;
        if (this.wasPlaying) {
          this.wavesurfer.play();
          this.wasPlaying = false;
        }
      });
      this.wavesurfer.on("finish", () => {
        this.$emit("finished");
        this.wasPlaying = true;
      });
    },
    async loadNewWave(wave) {
      this.wavesurfer.load(wave["url"]);
    },
    togglePlay() {
      this.wavesurfer.playPause();
    },
  },
  watch: {
    file: function (curr) {
      if (this.wavesurfer.isPlaying()) this.wasPlaying = true;
      this.loadNewWave(curr);
    },
  },
};
</script>

<style>
.loaded {
  background-color: green;
}
</style>
