<template>
  <MenuIcon :open="open" @menu-clicked="changeMenuState" />
  <div class="nav-menu" :class="{ 'menu-open': open }">
    <router-link
      v-for="route in $router.options.routes.filter(
        (r) => !['/lyrics'].includes(r['path'])
      )"
      :key="route['name']"
      @click="changeMenuState"
      :to="route['path']"
      >{{ route["name"] }}</router-link
    >
  </div>
</template>

<script>
import MenuIcon from "@/components/site/menu-icon.vue";

export default {
  name: "NavMenu",
  components: {
    MenuIcon,
  },
  data() {
    return {
      open: false,
    };
  },
  methods: {
    changeMenuState() {
      this.open = !this.open;
    },
  },
};
</script>

<style>
.nav-menu {
  width: 15%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background-color: black;
  transform: translateX(-100%);
  transition: 500ms;
  display: flex;
  flex-direction: column;
  z-index: 10;
}

@media screen and (max-width: 800px) {
  .nav-menu {
    width: 30%;
  }
}

.menu-open {
  transform: translateX(0);
}

