  text-decoration: none;
  color: white;
  text-align: center;
  position: relative;
  top: 80px;
  font-size: 20px;
  display: block;
  margin: 30px 0 30px 0;
  text-decoration: none;
  font-family: sans-serif;
}

.nav-menu > a:visited {
  color: white;
}
.nav-menu > a:hover {
  color: white;
}
.nav-menu > a:active {
  color: white;
}
</style>
